.title {
    font-size: 2rem;
    margin-top: 0;
    font-family: var(--title);
}

.subtittle {
    font-size: 1.5rem;
    margin-top: 0;
    font-family: var(--title);
    margin-bottom: .5rem;
}

.text {
    font-size: 1rem;
    font-family: var(--text);
}

.line-chart,
.scatter,
.bar-negative-chart,
.line-population {
    position: relative;
    text {
        font-family: var(--text);
        font-size: 14px;
    }
    .line {
        fill: none;
        stroke-width: 3;
        stroke: #b60b46;
    }
    .axis-y {
        .domain {
            stroke: none;
        }
        line {
            stroke-dasharray: 3px;
        }
    }
    .legend {
        font-weight: bold;
    }
}

.overlay {
    fill: transparent;
}

.tooltip {
    padding: .5rem;
    background-color: var(--white);
    border-radius: 4px;
    position: absolute;
    opacity: 1;
    box-shadow: var(--shadow);
}
.tooltip-number {
    font-weight: bold;
}

.scatter {
    .tooltip-citi,
    .tootlip-population,
    .tootlip-under,
    .tootlip-over {
        font-size: 14px;
        font-family: var(--text);
        margin: .25em;
        + p {
            display: none;

        }
    }
    .tooltip-citi {
        font-size: 1rem;
        font-weight: bold;
    }
}

.scatter-circles {
    fill: var(--scatter);
    fill-opacity: .6;
}

.line-negative {
    stroke: var(--line-negative);
    stroke-width: 3;
    fill: none;
}

.bar-negative {
    position: relative;
    .tooltip-year,
    .tooltip-born,
    .tooltip-deceased {
        font-size: 14px;
        font-family: var(--text);
        margin: .25em;
        + p {
            display: none;

        }
    }
    .tooltip-year {
        font-size: 1rem;
    }
}

.bar-negative-chart {
    .negative {
        fill: #a01721;
    }
    .positive {
        fill: #090387;
    }

}

.lines {
    fill: none;
    stroke: #0e2153;
    stroke-width: 2px;
}

.tooltip-media-texto {
    font-family: var(--text);
    font-size: 14px;
}
